.hamburger--open .hamburger__top-bar,.hamburger--open .hamburger__bottom-bar{opacity:0
}
.hamburger--open .hamburger__middle-bar{-ms-transform:rotate(-45deg);transform:rotate(-45deg)
}
.hamburger--open .hamburger__middle-bar-2{-ms-transform:rotate(45deg);transform:rotate(45deg)
}
.hamburger__middle-bar{transition:transform 0.3s cubic-bezier(0.2, 0, 0, 1) 0.1s;-ms-transform-origin:center center;transform-origin:center center
}
.hamburger__middle-bar-2{transition:transform 0.3s cubic-bezier(0.2, 0, 0, 1);-ms-transform-origin:center center;transform-origin:center center
}
.hamburger__top-bar,.hamburger__bottom-bar{opacity:1;transition:opacity 0.3s cubic-bezier(0.2, 0, 0, 1)
